@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,600;1,300&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;


@layer components{

  /*about sub-pages*/

  .imageContainer{
    @apply flex flex-col items-center bg-no-repeat mt-32 mb-32 bg-center md:mt-60 md:mb-60;
  }
  .textContainerOverlay{
    @apply bg-techTextActive opacity-75 w-3/4 text-techBlueLow p-4 text-center sm:px-6 md:px-16
  }
  .genericListItem{
    @apply md:inline text-lg md:p-2;
  }
  .seeThroughText{
    @apply text-center text-black mix-blend-overlay;
  }
  .titleTextLarge{
    @apply p-6 text-6xl text-center text-techTextActive;
  }
  .titleTextSmall{
    @apply text-center text-lg text-techTextActive;
  }

  /*icon stuff*/

  .iconText{
    @apply inline-block align-top mt-5 ml-2;
  }
  .defaultIcon{
    @apply p-2 inline-block;
  }

  /* 3 section selector*/

  .sectionContainer{
    @apply w-full px-2 lg:w-3/4;
  }
  .sectionLiElement{
    @apply w-1/3 inline-block cursor-pointer;
  }
  .sectionTitle{
    @apply pb-4 px-0 md:px-4 text-lg lg:text-xl text-center border-b-2;
  }

}